import React, { Component } from 'react';
import {Container, Row, Col, Form, Button} from 'react-bootstrap';
import { FIRESTORE_CONFIG } from '../config/config';
import firebase from 'firebase';

const firebaseApp = firebase.initializeApp(FIRESTORE_CONFIG);
const db = firebase.firestore();

export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      updatedCollection: "",
      collectionData: ""
    }
  }

  handleCollection = (event) => {
    this.setState({updatedCollection: event.target.value}, async () => {
      this.setState({
        collectionData: this.state.updatedCollection === "" ? "" : JSON.stringify(
          await db.collection(this.state.updatedCollection).get()
          .then((collection) => {
              return collection.docs.map(doc => doc.data());
          })
        )
      });
    });
  }

  handleData = (event) => {
    this.setState({collectionData: event.target.value});
  }

  async componentDidMount() {

  }

  render() {
    return(
      <Container fluid id="admin" className="px-5 py-1 min-vh-100 overflow-auto">
        <Row className="w-100"><h1>Admin Page</h1></Row>
        <Row className="w-100 p-0 d-flex align-items-center justify-content-center h-100">
          <Form>
            <Form.Group controlId="updatedCollection">
              <Form.Control as="select" value={this.state.updatedCollection} onChange={this.handleCollection}>
                <option value="">Update...</option>
                <option value="introduction">Background</option>
                <option value="achievements">Achievements</option>
                <option value="projects">Projects</option>
                <option value="courses">Courses</option>
              </Form.Control>
              <Button variant="success" className="mt-1">Submit</Button>
            </Form.Group>
            <Form.Group controlId="collectionData" className="mt-3">
              <Form.Label>data in JSON</Form.Label>
              <Form.Control 
                as="textarea" 
                rows={15} 
                disabled={this.state.updatedCollection === ""}
                value={this.state.collectionData} 
                onChange={this.handleData}
              />
            </Form.Group>
          </Form>
        </Row>
      </Container>
    );
  }
}